import React from 'react'
import './style.less'
class CityList extends React.Component {
 
	render() {
	    return (
            <div className=''>
                <ul className="cityList">
                    <li onClick={this.clickFun.bind(this,'')}><span></span></li>
                    <li onClick={this.clickFun.bind(this,'')}><span></span></li>
                    <li onClick={this.clickFun.bind(this,'')}><span></span></li>
                    <li onClick={this.clickFun.bind(this,'')}><span></span></li>
                    <li onClick={this.clickFun.bind(this,'')}><span></span></li>
                    <li onClick={this.clickFun.bind(this,'')}><span></span></li>
                    <li onClick={this.clickFun.bind(this,'')}><span></span></li>
                    <li onClick={this.clickFun.bind(this,'')}><span></span></li>
                </ul>
            </div>
	    )
	}
  clickFun(cityName){
     this.props.changCityFun(cityName)
  }
	
}

export default CityList




