import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import ReactSwipe from 'react-swipe'
import { Link } from 'react-router'

import './style.less'

class Category extends React.Component {
    /*
     https://github.com/voronianski/react-swipe 
    `npm install react swipe-js-iso react-swipe --save`
    */
    constructor(props, context) {
        super(props, context);
        this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
        this.state = {
            index: 0
        }
    }
    render() {
        const opt = {
            auto: 2500,
            callback: function (index) {
                // index
                this.setState({index: index});
            }.bind(this)
        }

        return (
            <div id="home-category">
                <ReactSwipe swipeOptions={opt}>
                    <div className="carousel-item">
                        <ul className="clear-fix">
                            <Link to="/search/jingdian"><li className="float-left jingdian"></li></Link>
                            <Link to="/search/ktv"><li className="float-left ktv">KTV</li></Link>
                            <Link to="/search/gouwu"><li className="float-left gouwu"></li></Link>
                            <Link to="/search/shenghuofuwu"><li className="float-left shenghuofuwu"></li></Link>
                            <Link to="/search/jianshenyundong"><li className="float-left jianshenyundong"></li></Link>
                            <Link to="/search/meifa"><li className="float-left meifa"></li></Link>
                            <Link to="/search/qinzi"><li className="float-left qinzi"></li></Link>
                            <Link to="/search/xiaochikuaican"><li className="float-left xiaochikuaican"></li></Link>
                            <Link to="/search/zizhucan"><li className="float-left zizhucan"></li></Link>
                            <Link to="/search/jiuba"><li className="float-left jiuba"></li></Link>
                        </ul>
                    </div>
                    <div className="carousel-item">
                        <ul className="clear-fix">
                            <Link to="/search/meishi"><li className="float-left meishi"></li></Link>
                            <Link to="/search/dianying"><li className="float-left dianying"></li></Link>
                            <Link to="/search/jiudian"><li className="float-left jiudian"></li></Link>
                            <Link to="/search/xuixianyule"><li className="float-left xuixianyule"></li></Link>
                            <Link to="/search/waimai"><li className="float-left waimai"></li></Link>
                            <Link to="/search/huoguo"><li className="float-left huoguo"></li></Link>
                            <Link to="/search/liren"><li className="float-left liren"></li></Link>
                            <Link to="/search/dujiachuxing"><li className="float-left dujiachuxing"></li></Link>
                            <Link to="/search/zuliaoanmo"><li className="float-left zuliaoanmo"></li></Link>
                            <Link to="/search/zhoubianyou"><li className="float-left zhoubianyou"></li></Link>
                        </ul>
                    </div>
                    <div className="carousel-item">
                        <ul className="clear-fix">
                            <Link to="/search/ribencai"><li className="float-left ribencai"></li></Link>
                            <Link to="/search/spa"><li className="float-left SPA">SPA</li></Link>
                            <Link to="/search/jiehun"><li className="float-left jiehun"></li></Link>
                            <Link to="/search/xuexipeixun"><li className="float-left xuexipeixun"></li></Link>
                            <Link to="/search/xican"><li className="float-left xican"></li></Link>
                            <Link to="/search/huochejipiao"><li className="float-left huochejipiao"></li></Link>
                            <Link to="/search/shaokao"><li className="float-left shaokao"></li></Link>
                            <Link to="/search/jiazhuang"><li className="float-left jiazhuang"></li></Link>
                            <Link to="/search/chongwu"><li className="float-left chongwu"></li></Link>
                            <Link to="/search/all"><li className="float-left quanbufenlei"></li></Link>
                        </ul>
                    </div>
                </ReactSwipe>
                <div className="index-container">
                    <ul>
                        <li className={this.state.index === 0 ? "selected" : ''}></li>
                        <li className={this.state.index === 1 ? "selected" : ''}></li>
                        <li className={this.state.index === 2 ? "selected" : ''}></li>
                    </ul>
                </div>
            </div>
        )
    }
}

export default Category


