export default {
    getItem: function (key) {
        let value
        try {
            value = localStorage.getItem(key)
        } catch (ex) {
            // error
            if (__DEV__) {
                console.error('localStorage.getItem, ', ex.message)
            }
        } finally {
            return value
        }
    },
    setItem: function (key, value) {
        try {
            // ios safari  localStorage.setItem 
            localStorage.setItem(key, value)
        } catch (ex) {
            //  error
            if (__DEV__) {
                console.error('localStorage.setItem, ', ex.message)
            }
        }
    }
}


